import React from 'react'
import { connect } from 'react-redux'
import { logout } from '../reducers/dudeReducer'

const LogoutButton = (props) => {
  console.log(props)
  return (
    <div>
      <button onClick={props.logout} name="logout">logout</button>
    </div>
  )
}

export default connect(
  null,
  { logout }

)(LogoutButton)


