import loginService from '../services/login'

const initialState = () => {
    const loggedUserJSON = window.localStorage.getItem('loggedDude')
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      loginService.setToken(user.token)
      return user
    }
    return null
}

const dudeReducer = (store = initialState(), action) => {
    switch (action.type) {
        case 'LOGIN':
            return action.dude
        case 'LOGOUT':
            return null
        default:
            return store
    }
}

export const login = (username, password) => {
    console.log('loggin')
    return async (dispatch) => {
        const dude = await loginService.login({
            username: username,
            password: password
        })
        window.localStorage.setItem('loggedDude', JSON.stringify(dude))
        loginService.setToken(dude.token)
        dispatch({
            type: 'LOGIN',
            dude
        })
    }
}

export const register = (username, password) => {
    console.log('reggin')
    return async (dispatch) => {
        const dude = await loginService.register({
            username: username,
            password: password
        })
        window.localStorage.setItem('loggedDude', JSON.stringify(dude))
        loginService.setToken(dude.token)
        dispatch({
            type: 'LOGIN',
            dude
        })
    }
}

export const logout = (username, password) => {
    console.log('logout')
    return async (dispatch) => {
        window.localStorage.removeItem('loggedDude')
        loginService.setToken(null)
        dispatch({
            type: 'LOGOUT'
        })
    }
}
export default dudeReducer


